import React, { Component } from 'react';
import {Polyline} from 'google-maps-react';

class Historic extends Component {
	constructor(props) {
		super(props);

		this.historicmap = React.createRef();
	}

	componentDidMount = () => {
		this.loadMap();
	}

	componentDidUpdate(prevProps, prevState) {
		if (prevProps.google !== this.props.google) {
			this.loadMap();
		}

		if (prevProps.favorities !== this.props.favorities) {
			this.loadMap();
		}
	}

	loadMap = () => {
		if (this.props.google) {
			console.log(this.props.favorities);

			var maps = this.props.google.maps;

			const {lat, lng} = this.props.favorities.center; //this.props.currentLocation || 

			const center = new maps.LatLng(lat, lng);
			const mapConfig = Object.assign({}, {
				center: center,
				zoom: this.props.favorities.zoom,
				zoomControl: false,
    			scaleControl: false,
    			fullscreenControl: false,
    			mapTypeControl: false,
    			streetViewControl: false,
			});

			this.map = new maps.Map(this.historicmap.current, mapConfig);

			var historic = new maps.Polygon({
	          paths: this.props.favorities.path,
	          strokeColor: '#4D8FAC',
	          strokeOpacity: 0.8,
	          strokeWeight: 4,
	        });
	        historic.setMap(this.map);
		}
	}

	

	// renderChildren = () => {
	// 	const {children} = this.props;

	// 	if (!children) return;

	// 	return React.Children.map(children, c => {
	// 		return React.cloneElement(c, {
	// 			map: this.map,
	// 			google: this.props.google,
	// 			mapCenter: this.props.favorities.center,
	// 		});
	// 	})
	// }

	render() {
		if (!this.props.favorities) return;

	  	return (
	  		<div 
    			ref={this.historicmap} 
    			style={{width: this.props.mapDivDimentions.width +"px", height: this.props.mapDivDimentions.height +"px", position: "relative"}} //transformOrigin='center', transform=`translate(-50%,-50%) rotate(${this.props.angle}deg)` //если крутить карту
	          	>
	          	
          	</div>
        );
	}
}

export default Historic;

